.projectDetailPage {
    background-color: white;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    @include forTablet {
        justify-content: flex-start;
    }

    

    .projectDetailContainer {
        display: flex;
        flex-direction: column;
        align-items: center;


        @include forTablet {
            height: calc(100vh - 7vh);
            flex-direction: row;
            justify-content: space-around;
        }

        .mockupProject {
            width: 90%;
            margin-right: 5%;

            @include forMobile {
                width: 100%;
            }


            @include forTablet {
                transform: none;
                width: 50%;
                margin-right: 0;
            }
        }

        .infoProjectDetailContainer {
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding: 0 12% 0 12%;
            z-index: 2;

            @include forPc {
                width: 50%;
                height: 100%;
                align-items: center;
                padding: 3% 6% 0 0;
                transform: none;
            }

            .titleProject {
                font-size: 1.7rem;

                @include forTablet {
                    font-size: 2.2rem;
                }
            }

            .tags {
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                margin-top: 15px;
                text-transform: uppercase;

                span {
                    margin-right: 7px;
                    margin-bottom: 4%;
                    font-size: 0.7rem;
                    border-radius: 15px;
                    padding: 3px 6px 3px 6px;
                    background-color: #FEC726;
                    color: white;
                }
            }


            .descriptionjProject {
                margin-bottom: 4%;
                text-align: center;
                line-height: 25px;
                font-size: 0.9rem;

                @include forTablet {
                    font-size: 1rem;
                    width: 77%;
                }
            }

            .titleDescription {
                padding-top: 1%;
                margin-bottom: 2%;
            }

            .linkContainer {
                margin-top: 10px;
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;
                font-size: 0.8rem;

                .buttonShowWeb {
                    color: black;
                    cursor: pointer;
                    padding: 1px 12px 1px 12px;
                    border: none;
                  
                    background-color: transparent;
                   
                    display: flex;
                    align-items: center;


                    .titleVisitWeb {
                        color: white;
                        padding-right: 6px;
                    }

                    @include forPc {
                        &:hover>* {
                            color: black;
                        }

                    }



                    .iconWebsite {
                        width: 30px;
                        height: 30px;
                    }
                }

                .buttonGithub {
                    display: flex;
                    align-items: center;
                    padding-left: 12px;
                    border-left: 1px solid black;

                    .iconGithub {
                        width: 27px;
                        height: 27px;

                    }

                    .titleVisitGithub {
                        color: white;
                        padding-left: 6px;
                    }

                    @include forPc {
                        &:hover>* {
                            color: black;
                        }
                    }
                }



            }
        }
    }

    .panelNavigation {

        .hidden{
            color: #cdcccc !important;
            cursor:default;
    
            &:hover{
                border-bottom: none !important;
                font-weight: initial !important;
            }
        }

        cursor: pointer;
        
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 0.8rem;

        &:active{
            -webkit-tap-highlight-color: transparent;
        }

        @include forMobile {
            font-size: 0.9rem;
        }

        @include forTablet {
            padding-bottom: 20px;
            font-size: 1rem;
        }

        a {
            color: black;
        }

        .prev {
            margin-right: 12px;

            &:hover {
                font-weight: 500;
                border-right: 0;
                border-bottom: 2px solid #FEC726;
            }
        }

        .next {
            margin-left: 12px;

            &:hover {
                font-weight: 500;
                border-bottom: 2px solid #FEC726;
                border-left: 0;
            }
        }

        .iconHome {
            border-left: 2px solid #FEC726;
            border-right: 2px solid #FEC726;
            padding: 0 10px 0 10px;

            &:hover {
                color: white;
                background-color: #FEC726;
            }
        }

        i {
            font-size: 13px;
            color: #FEC726;
            padding: 0 12px 0 12px;
        }
    }


}